/* ----------------------- */
/* -------Variables------- */
/* ------------------------*/
:root {
    --mainBlue: #0A1F3B;
    --black: #000;
    --white: #fff;
    --marginPage: 0 100px 0 100px;
}

@media screen and (max-width: 764px) {
    :root {
        --marginPage: 0 80px 0 80px;
    }
}

@media screen and (max-width: 576px) {
    :root {
        --marginPage: 0 24px 0 24px;
    }
}

@media screen and (max-width: 380px) {
    :root {
        --marginPage: 0 16px 0 16px;
    }
}

/* ---------------------- */
/* -------Reseteo-------- */
/* --------------------- -*/
* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

/* ------------------------------ */
/* -------Estilos del body------- */
/* ------------------------------ */
body {
    overflow-x: hidden;
}

.main {
    margin: var(--marginPage);
}

.space {
    margin: 24px 0 24px 0;
}

/* -------------------- */
/* -------Header------- */
/* ---------------------*/

.header {
    width: 100%;
    height: 80px;
    background-color: var(--mainBlue);
}

.header .header--img {
    margin: var(--marginPage);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: left;
    -ms-flex-pack: left;
    justify-content: left;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.header--img img {
    margin-top: 25px;
    width: 100px;
}

/* ------------------------- */
/* -------Reproductor------- */
/* ------------------------- */

.reproductor--portadaRepeat {
    display: none;
    width: 100%;
    height: 100%;
}

.reproductor--portadaRepeat img {
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.reproductor--portadaRepeat-block {
    width: 100%;
    height: 44px;
    background-color: var(--black);
}

.player-wrapper {
    position: relative;
    padding-top: 56.25%;
}

.react-player {
    position: absolute;
    top: 0;
    left: 0;
}

.reproductor--botonStop {
    width: 100%;
    height: 24px;
    background-color: var(--black);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 20px;
    cursor: pointer;
    visibility: hidden;
}

.reproductor--botonStop-icon {
    width: 16px;
    height: 16px;
    border-radius: 2px;
    background-color: var(--white);
}

/* -------------------- */
/* -------Footer------- */
/* -------------------- */
.footer {
    width: 100%;
    height: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 50px;
}

.footer--social {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    margin-bottom: 24px;
}


.footer--social a img {
    width: 32px;
    margin-right: 20px;
}

.footer--texto p {
    text-align: center;
}

@media screen and (max-width: 886px) {
    .footer {
        padding: 48px;
    }
}

@media screen and (max-width: 480px) {
    .footer {
        padding: 32px;
    }
}

@media screen and (max-width: 380px) {
    .footer {
        padding: 16px;
    }
}